.editablecontent{
    display:grid;
    grid-template-columns:1fr auto;
}
.editablecontent:not(.editing){
    cursor:default;
}

.editablecontent>.edit-button{
    margin-left:0.2em;
    cursor:pointer;
}
.editablecontent:not(:hover) .onhover{
    visibility:hidden;
}
.editablecontent.editing .text-content{
    display: none;
}
.editablecontent.editing textarea{
    display:block;
}
.editablecontent:not(.editing) textarea{
    display:none;
}

.editablecontent .text-content{
    display:inline-block;
    position:relative;
}
.editablecontent svg *{
    pointer-events: none;
}
.editablecontent textarea{
    resize:none;
    width:100%;
    padding:0;
    margin:0;
    border:none;
    outline:thin black solid;
    font-weight: inherit;
    font-size: inherit;
    font-family: inherit;
